import React, { useState, useEffect } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, PieChart, Pie, Cell } from 'recharts';
import Map from './components/Map';
import { parseCSV } from './components/mapUtils';

// Import CSV files directly
import sampleCsv from './assets/data/sample.csv';
import greenScoreCsv from './assets/data/green_score.csv';
import stlTestCsv from './assets/data/stl_test.csv';

function App() {
  const [controlBarOpen, setControlBarOpen] = useState(true);
  const [showChart, setShowChart] = useState(false);
  const [showStats, setShowStats] = useState(false);
  const [scoreRangeYes, setScoreRangeYes] = useState(true);
  const [scoreRangeNo, setScoreRangeNo] = useState(true);
  const [scoreRangeMin, setScoreRangeMin] = useState(1);
  const [scoreRangeMax, setScoreRangeMax] = useState(10);
  const [csvData, setCsvData] = useState([]);
  const [csvFiles] = useState(['sample.csv', 'green_score.csv', 'stl_test.csv']); // Hardcoded CSV filenames
  const [currentDataset, setCurrentDataset] = useState('sample.csv');

  // Load CSV data when dataset changes
  useEffect(() => {
    if (!currentDataset) return;
    
    // Get the appropriate CSV content based on the selected dataset
    let csvContent;
    switch (currentDataset) {
      case 'sample.csv':
        csvContent = sampleCsv;
        break;
      case 'green_score.csv':
        csvContent = greenScoreCsv;
        break;
      case 'stl_test.csv':
        csvContent = stlTestCsv;
        break;
      default:
        csvContent = sampleCsv;
    }
    
    setCsvData(parseCSV(csvContent));
  }, [currentDataset]);

  // Sample data for charts
  const chartData = [
    { name: 'Yes', value: csvData.filter(row => {
      const score = row.scores || row.score;
      return score && score.toString().toLowerCase() === 'yes';
    }).length },
    { name: 'No', value: csvData.filter(row => {
      const score = row.scores || row.score;
      return score && score.toString().toLowerCase() === 'no';
    }).length }
  ];

  const numericData = csvData
    .filter(row => {
      const score = row.score;
      return score && !isNaN(parseFloat(score)) && parseFloat(score) >= 1 && parseFloat(score) <= 10;
    })
    .map(row => ({
      score: parseFloat(row.score),
      count: 1
    }))
    .reduce((acc, item) => {
      const existing = acc.find(x => x.score === item.score);
      if (existing) {
        existing.count += 1;
      } else {
        acc.push(item);
      }
      return acc;
    }, [])
    .sort((a, b) => a.score - b.score);

  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8'];

  return (
    <div style={{ padding: 20, fontFamily: 'Arial, sans-serif' }}>
      <div style={{ marginBottom: 20, textAlign: 'center' }}>
        <h1 style={{ color: '#333', marginBottom: 10 }}>Dynamic CSV Map Visualization</h1>
        <p style={{ color: '#666', marginBottom: 20 }}>
          This app now supports dynamic CSV rendering. Any CSV file with latitude/longitude and score columns will be automatically parsed and displayed.
        </p>

        <button
          onClick={() => setControlBarOpen(!controlBarOpen)}
          style={{
            padding: '10px 20px',
            backgroundColor: controlBarOpen ? '#dc3545' : '#28a745',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            fontSize: '16px',
            marginBottom: 20
          }}
        >
          {controlBarOpen ? 'Hide Controls' : 'Show Controls'}
        </button>
      </div>

      <Map
        controlBarOpen={controlBarOpen}
        setControlBarOpen={setControlBarOpen}
        showChart={showChart}
        setShowChart={setShowChart}
        showStats={showStats}
        setShowStats={setShowStats}
        scoreRangeYes={scoreRangeYes}
        setScoreRangeYes={setScoreRangeYes}
        scoreRangeNo={scoreRangeNo}
        setScoreRangeNo={setScoreRangeNo}
        scoreRangeMin={scoreRangeMin}
        setScoreRangeMin={setScoreRangeMin}
        scoreRangeMax={scoreRangeMax}
        setScoreRangeMax={setScoreRangeMax}
        csvData={csvData}
        currentDataset={currentDataset}
        setCurrentDataset={setCurrentDataset}
        csvFiles={csvFiles}
      />

      {showChart && (
        <div style={{ marginTop: 20, padding: 20, backgroundColor: '#f8f9fa', borderRadius: '10px' }}>
          <h2 style={{ color: '#333', marginBottom: 20 }}>Data Visualization</h2>
          
          <div style={{ display: 'flex', gap: 20, flexWrap: 'wrap' }}>
            {/* Pie Chart for Yes/No data */}
            {chartData.some(item => item.value > 0) && (
              <div style={{ flex: 1, minWidth: 300 }}>
                <h3 style={{ color: '#555', marginBottom: 10 }}>Yes/No Distribution</h3>
                <PieChart width={300} height={200}>
                  <Pie
                    data={chartData}
                    cx={150}
                    cy={100}
                    labelLine={false}
                    label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                    outerRadius={80}
                    fill="#8884d8"
                    dataKey="value"
                  >
                    {chartData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip />
                </PieChart>
              </div>
            )}

            {/* Bar Chart for numeric data */}
            {numericData.length > 0 && (
              <div style={{ flex: 1, minWidth: 300 }}>
                <h3 style={{ color: '#555', marginBottom: 10 }}>Score Distribution (1-10)</h3>
                <BarChart width={400} height={200} data={numericData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="score" />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="count" fill="#8884d8" />
                </BarChart>
              </div>
            )}
          </div>
        </div>
      )}

      {showStats && (
        <div style={{ marginTop: 20, padding: 20, backgroundColor: '#e9ecef', borderRadius: '10px' }}>
          <h2 style={{ color: '#333', marginBottom: 20 }}>Statistics</h2>
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: 20 }}>
            <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
              <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>Total Points</h3>
              <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#007bff', margin: 0 }}>{csvData.length}</p>
            </div>
            
            {chartData.some(item => item.value > 0) && (
              <>
                <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
                  <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>Yes Responses</h3>
                  <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#28a745', margin: 0 }}>
                    {chartData.find(item => item.name === 'Yes')?.value || 0}
                  </p>
                </div>
                <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
                  <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>No Responses</h3>
                  <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#dc3545', margin: 0 }}>
                    {chartData.find(item => item.name === 'No')?.value || 0}
                  </p>
                </div>
              </>
            )}

            {numericData.length > 0 && (
              <>
                <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
                  <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>Average Score</h3>
                  <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#17a2b8', margin: 0 }}>
                    {(numericData.reduce((sum, item) => sum + (item.score * item.count), 0) / 
                      numericData.reduce((sum, item) => sum + item.count, 0)).toFixed(2)}
                  </p>
                </div>
                <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
                  <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>Highest Score</h3>
                  <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#ffc107', margin: 0 }}>
                    {Math.max(...numericData.map(item => item.score))}
                  </p>
                </div>
                <div style={{ textAlign: 'center', padding: '15px', backgroundColor: 'white', borderRadius: '8px' }}>
                  <h3 style={{ color: '#555', margin: '0 0 10px 0' }}>Lowest Score</h3>
                  <p style={{ fontSize: '24px', fontWeight: 'bold', color: '#6f42c1', margin: 0 }}>
                    {Math.min(...numericData.map(item => item.score))}
                  </p>
                </div>
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
