// src/components/HeatmapLayerWrapper.js
import React from 'react';
import { HeatmapLayer } from '@react-google-maps/api';
import { detectCSVFormat } from './mapUtils';

export function HeatmapLayerWrapper({ 
  showHeatmap, 
  csvData, 
  heatmapRadiusLevel 
}) {
  // Prepare heatmap data safely, with weights
  let heatmapData = [];
  
  if (showHeatmap && window.google && window.google.maps && csvData.length > 0) {
    const csvFormat = detectCSVFormat(csvData);
    
    if (csvFormat) {
      const { latCol, lngCol, scoreCol, scoreFormat } = csvFormat;
      
      heatmapData = csvData
        .map(row => {
          const lat = parseFloat(row[latCol]);
          const lng = parseFloat(row[lngCol]);
          const score = row[scoreCol];
          
          if (isNaN(lat) || isNaN(lng)) return null;
          
          let weight;
          if (scoreFormat === 'yes_no') {
            const scoreLower = score.toString().toLowerCase();
            weight = scoreLower === 'yes' ? 5 : 0; // Use 5 for 'yes', 0 for 'no'
          } else if (scoreFormat === 'numeric_1_10') {
            const numScore = parseFloat(score);
            if (isNaN(numScore)) return null;
            weight = numScore * 2; // Amplify the score weighting
          } else {
            weight = 1; // Default weight for other formats
          }
          
          return { location: new window.google.maps.LatLng(lat, lng), weight };
        })
        .filter(Boolean);
    }
  }

  return (
    <HeatmapLayer 
      data={showHeatmap ? heatmapData : []} 
      options={{ 
        radius: 10 + (heatmapRadiusLevel - 1) * 10, 
        opacity: 0.8,
        gradient: [
          'rgba(0, 255, 255, 0)',
          'rgba(0, 255, 255, 1)',
          'rgba(0, 191, 255, 1)',
          'rgba(0, 127, 255, 1)',
          'rgba(0, 63, 255, 1)',
          'rgba(0, 0, 255, 1)',
          'rgba(0, 0, 223, 1)',
          'rgba(0, 0, 191, 1)',
          'rgba(0, 0, 159, 1)',
          'rgba(0, 0, 127, 1)',
          'rgba(63, 0, 91, 1)',
          'rgba(127, 0, 63, 1)',
          'rgba(191, 0, 31, 1)',
          'rgba(255, 0, 0, 1)'
        ]
      }} 
    />
  );
} 