// src/components/GeoJsonLoader.js
import { useEffect, useRef } from 'react';
import censusTractsGeoJson from '../assets/data/census_tracts.geojson.json';

export function GeoJsonLoader({ mapRef, showCensusTracts }) {
  const dataLayerRef = useRef(null);

  useEffect(() => {
    if (!mapRef.current || !showCensusTracts) {
      if (dataLayerRef.current) {
        dataLayerRef.current.setMap(null);
        dataLayerRef.current = null;
      }
      return;
    }

    // Use the imported GeoJSON data directly
    const dataLayer = new window.google.maps.Data({
      map: mapRef.current,
      style: {
        fillColor: '#cccccc',
        fillOpacity: 0.5,
        strokeColor: '#555555',
        strokeWeight: 1
      }
    });

    dataLayer.addGeoJson(censusTractsGeoJson);
    dataLayerRef.current = dataLayer;

    return () => {
      if (dataLayerRef.current) {
        dataLayerRef.current.setMap(null);
        dataLayerRef.current = null;
      }
    };
  }, [mapRef, showCensusTracts]);

  return null;
} 