// CSV parsing helper
export function parseCSV(text) {
  const lines = text.trim().split('\n');
  const headers = lines[0].split(',');
  return lines.slice(1).map(line => {
    const values = line.split(',');
    const obj = {};
    headers.forEach((h, i) => { obj[h.trim()] = values[i] ? values[i].trim() : ''; });
    return obj;
  });
}

// Detect CSV format and return metadata
export function detectCSVFormat(data) {
  if (!data || data.length === 0) return null;
  
  const firstRow = data[0];
  const headers = Object.keys(firstRow);
  
  // Check for latitude/longitude columns
  const hasLatLng = headers.some(h => h.toLowerCase().includes('lat')) && 
                   headers.some(h => h.toLowerCase().includes('lng'));
  
  const hasLatLons = headers.some(h => h.toLowerCase().includes('lat')) && 
                    headers.some(h => h.toLowerCase().includes('lon'));
  
  const hasLatitudeLongitude = headers.some(h => h.toLowerCase().includes('latitude')) && 
                              headers.some(h => h.toLowerCase().includes('longitude'));
  
  if (!hasLatLng && !hasLatLons && !hasLatitudeLongitude) {
    return null; // No valid coordinate columns found
  }
  
  // Determine coordinate column names
  let latCol, lngCol;
  if (hasLatLng) {
    latCol = headers.find(h => h.toLowerCase().includes('lat'));
    lngCol = headers.find(h => h.toLowerCase().includes('lng'));
  } else if (hasLatLons) {
    latCol = headers.find(h => h.toLowerCase().includes('lat'));
    lngCol = headers.find(h => h.toLowerCase().includes('lon'));
  } else {
    latCol = headers.find(h => h.toLowerCase().includes('latitude'));
    lngCol = headers.find(h => h.toLowerCase().includes('longitude'));
  }
  
  // Check for score column
  const scoreCol = headers.find(h => 
    h.toLowerCase().includes('score') || 
    h.toLowerCase().includes('scores')
  );
  
  if (!scoreCol) {
    return null; // No score column found
  }
  
  // Determine score format
  const firstScore = firstRow[scoreCol];
  const isNumeric = !isNaN(parseFloat(firstScore));
  
  let scoreFormat;
  if (isNumeric) {
    const score = parseFloat(firstScore);
    if (score >= 1 && score <= 10) {
      scoreFormat = 'numeric_1_10';
    } else {
      scoreFormat = 'numeric_other';
    }
  } else {
    const scoreLower = firstScore.toLowerCase();
    if (scoreLower === 'yes' || scoreLower === 'no') {
      scoreFormat = 'yes_no';
    } else {
      scoreFormat = 'categorical';
    }
  }
  
  return {
    latCol,
    lngCol,
    scoreCol,
    scoreFormat,
    headers
  };
}

// Get color for score based on format
export function getScoreColor(score, format) {
  if (format === 'yes_no') {
    const scoreLower = score.toString().toLowerCase();
    return scoreLower === 'yes' ? 'green' : 'red';
  } else if (format === 'numeric_1_10') {
    const numScore = parseFloat(score);
    if (isNaN(numScore)) return '#cccccc';
    
    // Create RGB gradient from red (1) to green (10)
    const normalized = (numScore - 1) / 9; // 0 to 1
    const red = Math.round(255 * (1 - normalized));
    const green = Math.round(255 * normalized);
    const blue = 0;
    
    return `rgb(${red}, ${green}, ${blue})`;
  } else {
    return '#cccccc'; // Default gray for other formats
  }
}

// Get marker size for score
export function getMarkerSize(score, format) {
  if (format === 'yes_no') {
    return 6; // Same size as original
  } else if (format === 'numeric_1_10') {
    const numScore = parseFloat(score);
    if (isNaN(numScore)) return 6;
    // Scale from 6 to 12 based on score
    return 6 + ((numScore - 1) / 9) * 6;
  } else {
    return 6; // Default size
  }
}

// Distance calculation helper
export function calculateDistance(point1, point2) {
  const R = 6371; // Earth's radius in kilometers
  const dLat = (point2.lat - point1.lat) * Math.PI / 180;
  const dLon = (point2.lng - point1.lng) * Math.PI / 180;
  const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
            Math.cos(point1.lat * Math.PI / 180) * Math.cos(point2.lat * Math.PI / 180) *
            Math.sin(dLon/2) * Math.sin(dLon/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  return R * c;
}

// Export CSV helper
export function exportToCSV(data, filename) {
  if (data.length === 0) return;
  const headers = Object.keys(data[0] || {});
  const csvContent = [
    headers.join(','),
    ...data.map(row => headers.map(header => {
      const value = row[header];
      return typeof value === 'string' ? `"${value.replace(/"/g, '""')}"` : value;
    }).join(','))
  ];
  const csvString = csvContent.join('\n');
  const blob = new Blob([csvString], { type: 'text/csv' });
  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = `${filename}-${new Date().toISOString().slice(0, 10)}.csv`;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
  URL.revokeObjectURL(url);
}

// Auto-detect CSV files in public folder
export async function detectAvailableCSVs() {
  const knownCSVs = [
    { filename: 'sample.csv', displayName: 'Sample (Yes/No)', format: 'yes_no' },
    { filename: 'green_score.csv', displayName: 'Green Score (1-10)', format: 'numeric_1_10' },
    { filename: 'stl_test.csv', displayName: 'STL Test (1-10)', format: 'numeric_1_10' }
  ];
  
  const availableCSVs = [];
  
  for (const csv of knownCSVs) {
    try {
      const response = await fetch(process.env.PUBLIC_URL + '/' + csv.filename);
      if (response.ok) {
        const text = await response.text();
        const parsed = parseCSV(text);
        const format = detectCSVFormat(parsed);
        
        if (format) {
          availableCSVs.push({
            ...csv,
            actualFormat: format.scoreFormat,
            rowCount: parsed.length
          });
        }
      }
    } catch (error) {
      console.log(`CSV ${csv.filename} not found or invalid`);
    }
  }
  
  return availableCSVs;
}

// Get display name for any CSV file
export function getCSVDisplayName(filename, format) {
  // Remove .csv extension
  const name = filename.replace('.csv', '');
  
  // Convert to readable format
  const displayName = name
    .split(/[-_]/)
    .map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
    .join(' ');
  
  // Add format indicator
  if (format === 'yes_no') {
    return `${displayName} (Yes/No)`;
  } else if (format === 'numeric_1_10') {
    return `${displayName} (1-10)`;
  } else {
    return `${displayName} (${format})`;
  }
} 